import axios from 'axios'

const axiosInit = axios.create()
const baseURL = 'https://news-at.zhihu.com/api' //后台路径（这是一个开源的段子接口）
const httpHelper = {}
// 封装get方法（post类似）
httpHelper.get = (apiUrl, params, succCallback, errCallback) => {
    let httpUrl = baseURL + apiUrl
    axiosInit.get(httpUrl, params).then(
        (res) => {
            if (succCallback) {
                succCallback(res.data)
            } else {
                console.log(res.data)
            }
        },
        (err) => {
            // alert(httpUrl)
            if (errCallback) {
                errCallback(err)
            }
        }
    )
}

httpHelper.post = (apiUrl, params, succCallback, errCallback) => {
    let httpUrl = baseURL + apiUrl
    axiosInit.post(httpUrl, params).then(
        (res) => {
            if (succCallback) {
                succCallback(res.data)
            } else {
                console.log(res.data)
            }
        },
        (err) => {
            // alert(httpUrl)
            if (errCallback) {
                errCallback(err)
            }
        }
    )
}

export default httpHelper

