<script setup>
import httpHelper from '../api/httpHelper';
import {onMounted, ref} from "vue";
import {ElLoading} from "element-plus";

const data = ref();
const y = ref('');
const ytime = ref('');

async function dayChange(){
    const timemap = Date.parse(y.value)/1000;
    console.log(timemap);

    const loading = ElLoading.service({
        lock: true,
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.3)',
    })
    await httpHelper.get('/3/section/2/before/'+timemap, '', function (res) {
        if (res.name === '瞎扯') {
            console.log(res);
            data.value = res.stories;
            loading.close()
        } else {
            console.log("接口错误!");
        }
    })
}

onMounted(async () => {

    const currentDate = new Date();   // 获取当前日期和时间
    const currentYear = currentDate.getFullYear();   // 获取当前年份

    const currentMonth = currentDate.getMonth() + 1;   // 获取当前月份（注意月份从 0 开始，所以需要加 1）

    const currentDay = currentDate.getDate();   // 获取当前日期
    const datetime = currentYear+'-'+currentMonth+'-'+currentDay;
    y.value = datetime;
    const timemap = Date.parse(datetime)/1000;
    ytime.value = timemap;
    //console.log(timemap);


    const loading = ElLoading.service({
        lock: true,
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.3)',
    })
    await httpHelper.get('/3/section/2/before/'+ytime.value, '', function (res) {
        if (res.name === '瞎扯') {
            console.log(res);
            data.value = res.stories;
            loading.close()
        } else {
            console.log("接口错误!");
        }
    })
})
</script>

<template>
    <div class="centent">
        <div class="title">瞎扯 · 如何正确地吐槽-合集</div>
        <div class="con">
            <div class="left">
                    <el-date-picker
                        v-model="y"
                        type="month"
                        placeholder="选择月份"
                        format="YYYY-MM-DD"
                        @change="dayChange"
                    />
            </div>
            <div class="main">

                <div v-for="item in data" :key="item" class="note">
                    <div class="imgs">
                        <a :href="item.url">
                            <el-avatar shape="square" :size="100" fit="cover" :src="item.images"/>
                        </a>
                    </div>
                    <div class="texts">
                        <a :href="item.url">
                            <span class="tit">{{ item.display_date }}({{ item.date }})</span><br><br>
                            <span class="titl">{{ item.title }}</span><br>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

