<script setup>
import httpHelper from '../api/httpHelper';
import {onMounted, ref} from "vue";
import {ElLoading} from "element-plus";

const data = ref();
onMounted(async () => {
    const loading = ElLoading.service({
        lock: true,
        text: 'Loading',
        background: 'rgba(0, 0, 0, 0.3)',
    })
    await httpHelper.get('/4/news/9764058', '', function (res) {
        if (res) {
            console.log(res);
            data.value = res.body;
            loading.close()
        } else {
            console.log("接口错误!");
        }
    })
})
</script>

<template>
    <div class="centent" v-html="data">
    </div>
</template>

